.navbar {
  margin-bottom: 0;
  border-radius: 0;
  padding: 12px 10px;
  background: rgba(0, 0, 0, 0.9); }
  .navbar .navbar-collapse .navbar-nav {
    float: right; }
    .navbar .navbar-collapse .navbar-nav li a {
      color: #ffffff;
      padding-left: 20px;
      padding-right: 20px; }
    .navbar .navbar-collapse .navbar-nav .active a {
      box-sizing: content-box;
      background-color: inherit;
      position: relative; }
      .navbar .navbar-collapse .navbar-nav .active a:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        width: 100%;
        background: #fff; }
  .navbar .serve-channel {
    display: none;
    position: fixed;
    z-index: 100;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    justify-content: center;
    align-items: center; }
    .navbar .serve-channel img {
      width: 320px; }
